 #wptravel-blocks-featured-category-container {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    gap: 30px;

    .layout-handler {
        max-width: 500px;
        width: 300px;
    }
}

#wp-travel-blocks-trip-featured-category {
    &.layout-one{
        .layout-handler{
            width: 100%;

        }
    }
    display: flex;
    justify-content: center;
    width: 100%;
    transition: 0.25s ease-in-out all;
    position: relative;
    margin: 0 auto;
    a{
        width: 100%;
    }

    &.layout-one {
        &:hover {
            .wp-travel-blocks-trip-featured-category-img-container {
                box-shadow: 0 1px 10px 5px rgba(0, 0, 0, 0.15);
                cursor: pointer;
            }

            .wp-travel-blocks-trip-featured-category-footer {
                .wp-travel-blocks-trip-featured-category-left-info {
                    i{
                        margin-left: 5px;
                        transform: translateX(10px);
                    }
                }
            }
        }

        .wp-travel-blocks-trip-featured-category-img-container {
            width: 100%;
            height: 400px;
            border-radius: 4px;
            overflow: hidden;
            transition: 0.25s ease-in-out all;
    
            img {
                object-fit: cover;
                width: inherit;
                height: 100%;
                transition: 0.25s ease-in-out all;
            }
        }
    
        .wp-travel-blocks-trip-featured-category-footer {
            display: flex;
            justify-content: space-between;
            align-items: center;
            margin-top: 5px;
    
            .wp-travel-blocks-trip-featured-category-left-info {
                span {
                    font-weight: 500;
                    font-size: 20px;
    
                    a {
                        text-decoration: none;
                    }
                }
    
                i {
                    margin-left: 5px;
                    font-size: 20px;
                    transition: 0.25s ease-in-out all;
                }
            }
    
            .wp-travel-blocks-trip-featured-category-right-info {
                span {
                    margin-left: 0px;
                    font-size: 14px;
                    font-weight: 400;
                }
    
                i {
                    font-size: 13px;
                }
            }
        }
    }

    &.layout-two {
        flex-direction: column;
        &:hover {
            .wp-travel-blocks-trip-featured-category-img-container {
                box-shadow: 0 1px 10px 5px rgba(0, 0, 0, 0.15);
                cursor: pointer;
            }

            .wp-travel-blocks-trip-featured-category-footer {
                display: flex;
                justify-content: center;
                margin-top: 10px;

                .wp-travel-blocks-trip-featured-category-left-info {
                    i{
                        margin-left: 5px;
                        opacity: 1;
                        transform: translateX(10px);
                    }

                    span {
                        font-weight: 500;
                        font-size: 22px;
                        margin-left: 10px;
                    }
                }
            }
        }

        a {
            text-decoration: none;
        }
        
        .wp-travel-blocks-trip-featured-category-img-container {
            position: relative;
            width: 100%;
            height: 400px;
            border-radius: 4px;
            overflow: hidden;
            transition: 0.25s ease-in-out all;

            &:hover {
                box-shadow: 0 4px 10px 5px rgba(0, 0, 0, 0.15);
            }

            img {
                object-fit: cover;
                width: inherit;
                height: 100%;
                transition: 0.25s ease-in-out all;
            }
            

            .wp-travel-blocks-trip-featured-category-img-overlay-trip {
                position: absolute;
                bottom: 0;
                margin: 15px;
                background: rgba(0, 0, 0, 0.5);
                padding: 5px 10px;
                border-radius: 25px;
                backdrop-filter: blur(2px);
                opacity: 1;
                color: white;
                line-height: 1.2;
                font-size: 14px;
            }
        }

        .wp-travel-blocks-trip-featured-category-footer {
            display: flex;
            justify-content: center;
            margin-top: 10px;

            .wp-travel-blocks-trip-featured-category-left-info {
                i {
                    margin-left: 5px;
                    font-size: 20px;
                    opacity: 0;
                    transform: translateX(-10px);
                    transition: 0.25s ease-in-out all;
                }

                span {
                    font-weight: 500;
                    font-size: 22px;
                    margin-left: 30px;
                    transition: 0.25s ease-in-out all;
                }
            }

            .wp-travel-blocks-trip-featured-category-right-info {
                i {
                    font-size: 13px;
                    font-weight: bold;
                }

                span {
                    margin-left: 0px;
                    font-size: 14px;
                    font-weight: 400;
                }
            }
        }
    }

    &.layout-three {
        &:hover {
            .wp-travel-blocks-trip-featured-category-img-container {
                box-shadow: 0 4px 10px 5px rgba(0, 0, 0, 0.15);

                img {
                    object-fit: cover;
                    width: inherit;
                    height: 100%;
                }
            }

            .wp-travel-blocks-trip-featured-category-img-overlay-trip {
                .wp-travel-blocks-trip-featured-category-footer {
                    bottom: 5px;
                    position: absolute;
                    width: 100%;

                    .wp-travel-blocks-trip-info-container {
                        .wp-travel-blocks-trip-featured-category-right-info {
                            i {
                                transform: translateX(0px);
                            }
                        }
                    }
                }
            }
        }

        .wp-travel-blocks-trip-featured-category-img-container {
            display: flex;
            position: relative;
            width: 100%;
            height: 400px;
            border-radius: 4px;
            overflow: hidden;
            transition: 0.25s ease-in-out all;
            box-sizing: border-box;

            img {
                object-fit: cover;
                width: inherit;
                height: 100%;
                transition: 0.25s ease-in-out all;
            }            
        }

        .wp-travel-blocks-trip-featured-category-img-overlay-trip {
            bottom: 0;
            position: absolute;
            width: 100%;
            height: 100%;

            .wp-travel-blocks-trip-featured-category-footer {
                bottom: 0;
                position: absolute;
                width: 100%;
                transition: 0.25s ease-in-out all;

                .wp-travel-blocks-trip-info-container {
                    margin: 15px;
                    backdrop-filter: blur(4px);
                    color: white;
                    display: flex;
                    align-items: center;
                    justify-content: space-between;
                    padding: 10px 15px;
                    border-radius: 4px;
                    background-color: rgba(0, 0, 0, 0.63);

                    .wp-travel-blocks-trip-featured-category-left-info {
                        line-height: 1;

                        .wp-travel-blocks-trip-destination {
                            font-size: 20px;
                            font-weight: 500;
                            margin-bottom: 8px;
                            text-transform: capitalize;
                            line-height: 1;
                            
                            span {
                                line-height: 1;
                            }
                        }

                        
                        .wp-travel-blocks-trip-count {
                            font-size: 14px;
                            font-weight: 400;
                        }
                    }

                    .wp-travel-blocks-trip-featured-category-right-info {
                        i {
                            font-size: 22px;
                            transform: translateX(-10px);
                            transition: 0.25s ease-in-out all;
                        }
                    }
                }
            }
        }
    }

    &.layout-four {
        &:hover {
            .wp-travel-blocks-trip-featured-category-img-container {
                box-shadow: 0 4px 10px 5px rgba(0, 0, 0, 0.15);

                img {
                    object-fit: cover;
                    width: inherit;
                    height: 100%;
                }
            }

            .wp-travel-blocks-trip-featured-category-img-overlay-trip {
                .wp-travel-blocks-trip-featured-category-footer {
                    .wp-travel-blocks-trip-info-container {
                        .wp-travel-blocks-trip-featured-category-right-info {
                            i {
                                transform: translateX(0px);
                            }
                        }
                    }
                    .wp-travel-blocks-trip-featured-category-bottom {
                        .wp-travel-blocks-trip-featured-category-arrow {
                            scale: 1;
                            opacity: 1;
                        }
                    }
                }
            }
        }

        .wp-travel-blocks-trip-featured-category-img-container {
            display: flex;
            position: relative;
            width: 100%;
            height: 400px;
            border-radius: 4px;
            overflow: hidden;
            transition: 0.25s ease-in-out all;
            box-sizing: border-box;

            img {
                object-fit: cover;
                width: inherit;
                height: 100%;
                transition: 0.25s ease-in-out all;
            }
        }

        .wp-travel-blocks-trip-featured-category-img-overlay-trip {
            bottom: 0;
            position: absolute;
            width: 100%;
            height: 100%;

            .wp-travel-blocks-trip-featured-category-footer {
                display: flex;
                position: absolute;
                transition: 0.25s ease-in-out all;
                flex-direction: column;
                justify-content: space-between;
                height: 100%;
                width: 100%;

                .wp-travel-blocks-trip-info-container {
                    width: fit-content;
                    margin: 15px;
                    backdrop-filter: blur(4px);
                    color: white;
                    display: flex;
                    align-items: center;
                    justify-content: space-between;
                    padding: 7px 15px;
                    border-radius: 7px;
                    background-color: rgba(0, 0, 0, 0.63);
                    

                    .wp-travel-blocks-trip-featured-category-left-info {
                        line-height: 1;

                        .wp-travel-blocks-trip-destination {
                            font-size: 18px;
                            font-weight: 500;
                            text-transform: capitalize;
                            margin-bottom: 5px;

                            span {
                                font-size: 20px;
                                font-weight: 500;
                            }
                        }

                        span {
                            font-size: 13px;
                            font-weight: 400;
                        }

                        i {
                            font-size: 13px;
                        }
                    }

                    .wp-travel-blocks-trip-featured-category-right-info {
                        i {
                            font-size: 22px;
                            transform: translateX(-10px);
                            transition: 0.35s ease-in-out all;
                        }
                    }
                }                

                .wp-travel-blocks-trip-featured-category-bottom {
                    display: flex;
                    justify-content: flex-end;
                    margin: 15px;

                    .wp-travel-blocks-trip-featured-category-arrow {
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        width: 50px;
                        height: 50px;
                        border-radius: 7px;
                        background-color: rgba(0, 0, 0, 0.63);
                        scale: 0.9;
                        opacity: 0;
                        transition: 0.25s ease-in-out all;

                        i {
                            font-size: 20px;
                            display: flex;
                            color: white;
                        }
                    }
                }
            }
        }
    }
}